 .content {
	 position: relative;
	 display: flex;
	 flex-direction: column;
	 align-items: center;
	 width: 100%;
	 padding: 1rem;
	 transition: transform var(--d) var(--e);
	 z-index: 1;
}
 .content > * + * {
	 margin-top: 1rem;
}
 .title {
	 font-size: 1.2rem;
	 font-weight: bold;
	 line-height: 1.2;
}
.titledets {
	
	 z-index: 1; 
	font-size: 0.8rem;
	/* font-weight: bold; */
	line-height: 0.8;
}
.titledet {
	position: relative;
	 display: flex;
	 flex-direction: column;
	 align-items: center;
	 width: 100%;
	 padding: 1rem;
	 transition: transform var(--d) var(--e);
	 z-index: 1;
	font-size: 1.3rem;
	font-weight: bold;
	line-height: 1.2;
}
 .copy {
	 font-family: var(--font-serif);
	 font-size: 1.125rem;
	 font-style: italic;
	 line-height: 1.35;
}
.btn-page {
	cursor: pointer;
	margin-top: 1.5rem;
	padding: 0.75rem 0.60rem;
	font-size: 0.70rem;
	font-weight: bold;
	letter-spacing: 0.025rem;
	text-transform: uppercase;
	color: rgb(238, 234, 234);
	background-color: rgb(12, 12, 12);
	border: none;
}
 .btn {
	 cursor: pointer;
	 margin-top: 1.5rem;
	 padding: 0.75rem 0.60rem;
	 font-size: 0.70rem;
	 font-weight: bold;
	 letter-spacing: 0.025rem;
	 text-transform: uppercase;
	 color: rgb(238, 234, 234);
	 background-color: rgb(163, 154, 154);
	 border: none;
}
 .btn:hover {
	 background-color: #797676;
}
 .btn:focus {
	 outline: 1px dashed yellow;
	 outline-offset: 3px;
}
 @media (hover: hover) and (min-width: 600px) {
	 .card:after {
		 transform: translateY(0);
	}
	 .content {
		 transform: translateY(calc(100% - 4.5rem));
	}
	 .content > *:not(.title) {
		 opacity: 0;
		 transform: translateY(1rem);
		 transition: transform var(--d) var(--e), opacity var(--d) var(--e);
	}
	 .card:hover, .card:focus-within {
		 align-items: center;
	}
	 .card:hover:before, .card:focus-within:before {
		 transform: translateY(-4%);
	}
	 .card:hover:after, .card:focus-within:after {
		 transform: translateY(-50%);
	}
	 .card:hover .content, .card:focus-within .content {
		 transform: translateY(0);
	}
	 .card:hover .content > *:not(.title), .card:focus-within .content > *:not(.title) {
		 opacity: 1;
		 transform: translateY(0);
		 transition-delay: calc(var(--d) / 8);
	}
	 .card:focus-within:before, .card:focus-within:after, .card:focus-within .content, .card:focus-within .content > *:not(.title) {
		 transition-duration: 0s;
	}
}

.nav__cont {
	position: fixed;
	width: 60px;
	top: 0;
	height: 100vh;
	z-index: 100;
	background-color: #202020;
	overflow: hidden;
	transition: width 0.3s ease;
	cursor: pointer;
	box-shadow: 4px 7px 10px rgba(0, 0, 0, .4);
}
.nav__cont:hover {
	width: 200px;
}
@media screen and (min-width: 600px) {
	.nav__cont {
		width: 80px;
   }
}
.nav {
	list-style-type: none;
	color: white;
}
.nav:first-child {
	padding-top: 1.5rem;
}
.nav__items {
	padding-bottom: 4rem;
	font-family: 'roboto';
}
.nav__items a {
	position: relative;
	display: block;
	top: -35px;
	padding-left: 25px;
	padding-right: 15px;
	transition: all 0.3s ease;
	margin-left: 25px;
	margin-right: 10px;
	text-decoration: none;
	color: white;
	font-family: 'roboto';
	font-weight: 100;
	font-size: 1.35em;
}
.nav__items a:after {
	content: '';
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	border-radius: 2px;
	background: radial-gradient(circle at 94.02% 88.03%, #54a4ff, transparent 100%);
	opacity: 0;
	transition: all 0.5s ease;
	z-index: -10;
}
.nav__items:hover a:after {
	opacity: 1;
}
.nav__items svg {
	width: 26px;
	height: 26px;
	position: relative;
	left: -25px;
	cursor: pointer;
}
@media screen and (min-width: 600px) {
	.nav__items svg {
		width: 32px;
		height: 32px;
		left: -15px;
   }
}
